import React from 'react';

import LoginButtons from './LoginButtons';

export default class Login extends React.Component {
    render = () => (
        <div className="container py-5" style={{top: "auto"}}>
            <h2 className="text-center p-5">Welcome to Fortnite Store!</h2>
            <div className="row">
                <div className="col text-center my-auto">
                    <LoginButtons home={this.props.home}/>
                </div>
            </div>
        </div>
    );
}