import React from 'react';
import { NavLink, Route, Switch, Redirect } from 'react-router-dom';
import HomeContent from './Home/HomeContent';

import getFortniteItems from '../scripts/fortnite';
import fortnitemapping from '../configs/fortnite-categories';

export default class Home extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            tabs: null
        }
    }

    componentDidMount = async () => {
        getFortniteItems().then(result => {
            this.setState({tabs: result.items});
        }).catch(error => {
            this.setState({tabs: null});
        });
    }

    render = () => {
        if(this.state.tabs == null){
            return (<div className="row"></div>);
        }

        return (
            <div className="row">
                <div className="col-sm-12 col-md-3 col-lg-2 col-xl-2">
                    <nav className="nav navbar-dark bg-dark">
                        <ul className="navbar-nav p-3 list-unstyled">
                            {Object.keys(this.state.tabs).map(tab => (
                                <li key={tab} className="nav-item">
                                    <NavLink className="nav-link" activeClassName="active" to={this.props.path + "/" + tab}> {(fortnitemapping[tab] != null)? fortnitemapping[tab]: tab} </NavLink>
                                </li>
                            ))}
                        </ul>
                    </nav>
                </div>
                <div className="col-sm-12 col-md-9 col-lg-10 col-xl-10">
                        <Switch>
                            <Route exact path={this.props.path}>
                                <Redirect to={this.props.path + "/" + Object.keys(this.state.tabs)[0]}/>
                            </Route>
                            
                            <Route path={this.props.path + "/:tab"} render={
                                (props) => {
                                    return (
                                        <HomeContent name={props.match.params.tab} items={this.state.tabs[props.match.params.tab]}/>
                                    );
                                }
                            }/>
                        </Switch>
                </div>
            </div>);
    };
}