import React from 'react';
import {GoogleLogin} from 'react-google-login';
import { Redirect } from 'react-router-dom';
export default class LoginButtons extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            loggedIn: false
        }
    }

    googleLoggedInSuccess = (googleUser) => {
        var profile = googleUser.getBasicProfile();
        var data = {
            name: profile.getName(),
            email: profile.getEmail(),
            image: profile.getImageUrl(),
            token: googleUser.getAuthResponse().id_token,   
            fromGoogle: true
        }
        fetch("/api/sessions", {
                method: "POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            }).then(response => response.json()).then(json => {
                this.setState({loggedIn: true, redirect: this.props.home});
            }).catch(error => {
                console.log(error);
            });
    }

    googleLoggedInFailed = () => {
        alert("Unable to login with google. Try other login methods");
    }

    dummyLogin = () => {
        fetch('/api/sessions', {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({})
        }).then(response => response.json()).then(json => {
            alert("Dummy signed in");
        }).catch(error => {
            console.log(error);
        });
    }

    render = () => {
        if(this.state.redirect){
            return (<Redirect to={this.state.redirect}/>);
        }
        return (<div>
            {(<GoogleLogin
                className="mr-2"
                clientId="584685314295-m141d4bsqmmc15qq9mc7lfbfmrosng8i.apps.googleusercontent.com"
                buttonText="Sign in with Google"
                onSuccess={this.googleLoggedInSuccess}
                onFailure={this.googleLoggedInFailed}
                isSignedIn={true}
            />)}
        </div>);
    }
}