import Home from '../routes/Home';
import Logout from '../routes/Logout';
import Cart from '../routes/Cart';

let navlinks = [
    {
        name: "Home",
        href: "/home",
        route: Home
    },{
        name: "My Cart",
        href: "/cart",
        route: Cart
    },{
        name: "Logout",
        href: "/logout",
        route: Logout
    }
]

export default navlinks;