import React from 'react';
import { Link } from 'react-router-dom';

export default class HomeContent extends React.Component {
    render = () => (
        <div className="row">
            {
                this.props.items.map(item => {
                    if(item.price == 0){
                        item.price = 100;
                    }
                    return (<div key={item.id} className="col-lg-4 col-md-5 col-sm-6">
                        <div className="card my-2">
                            <div className="card-header">
                                {item.type}
                            </div>
                            <img src={item.images.background} alt={item.name} className="card-image-top image-fluid rounded" style={{height: "230px"}}/>
                            <div className="card-body">
                                <h5 className="card-title">{item.name}</h5>
                                <p className="card-text">{item.description}</p>
                                <Link 
                                    to={{
                                        pathname: "../cart",
                                        item
                                    }}   
                                    className="btn btn-success">
                                        Buy
                                </Link>
                                <p className="float-right">{"$"+item.price}</p>
                            </div>
                        </div>
                    </div>);
                })
            }
        </div>
    );
}