const FortniteAPI = require('fortnite-api-io');
const fortnite = new FortniteAPI("13fd547d-483005c8-d4944523-16cc9b95");

let items = null;
export default function getItems(){
    return new Promise(async function(resolve, reject){
        try{
            if(items != null){
                resolve(items);
                return;
            }
            items = await fortnite.listItems();
            resolve(items);
        }catch(error){
            reject(items);
        }
    });
}