import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import checkout from '../scripts/paysafecheckout';

class Cart extends React.Component {
    static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
    }

    constructor(props){
        super(props);
        let cart = [];
        if(props.cart != null && Array.isArray(props.cart)){
            cart = props.cart;
        }
        this.state = {
            cart
        }
    }

    componentDidMount = () => {
        const {location} = this.props;
        let item = location.item;
        if(item != null){
           let cart = this.state.cart;
            cart.push(item);
            this.setState({cart: cart}); 
        }
    }

    removeItem = (item) => {
        let cart = this.state.cart.filter(i => i.id != item.id);
        this.setState({cart});
    }

    placeOrder = (total) => {
        checkout(total, new Date().getTime()).then(result => {
            fetch('/api/payments/', {
                method: "POST",
                body: JSON.stringify(result)
            }).then(response => response.json()).then(json => {
                console.log(json);
            }).catch(error => {
                alert("Error while processing payment");
            })
        }).catch(error => {
            console.log(error);
            alert("PaySafe checkout failed");
        })
    }

    render = () => {
        let cart = this.state.cart;
        let cartDetails = cart.map(item => {
            return (
                <div key={item.id} className="card my-2">
                    <div className="row">
                        <div className="col-4">
                            <img src={item.images.background} alt={item.name} className="img-fluid" style={{width: "200px"}}/>
                        </div>
                        <div className="col-5">
                            <div className="card-body" style={{height: "85%"}}>
                                <h5 className="card-title">{item.name}</h5>
                                <section className="card-text">
                                    <p>{item.description}</p>
                                    <p><b>{item.type} (type)</b></p>
                                </section>
                            </div>
                        </div>
                        <div className="col-3 text-right">
                            <p className="card-text p-4 align-bottom">
                                <big className="text-muted">
                                    {"$" + item.price}
                                </big>
                            </p>
                            <button className="btn btn-danger btn-sm m-4" onClick={this.removeItem.bind(this, item)}>
                                &#10007;
                            </button>
                        </div>
                    </div>
                </div>
            );
        });
        
        let total = 0;
        let orderSummary = (
            <table className="table my-2" style={{height: "70%"}}>
                <thead>
                    <tr>
                        <th>Item</th>
                        <th className="text-right">Cost</th>
                    </tr>
                </thead>
                <tbody>
                {
                    this.state.cart.map(item => {
                        total += item.price;
                        return (<tr key={item.id}>
                            <td>{item.name}</td>
                            <td className="text-right">{"$" + item.price}</td>
                        </tr>);
                    })
                }
                </tbody>
                <tfoot>
                    <tr>
                        <td><i>Total</i></td>
                        <td className="text-right">{"$" + total}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td className="text-right">
                            <button className={"btn btn-outline-success" + ((cart.length == 0)? " disabled": "")} onClick={this.placeOrder.bind(this, total)}>Place Order</button>
                        </td>
                    </tr>
                </tfoot>
            </table>
        );
        if(cart.length == 0){
            cartDetails = <h2 className="text-muted text-center p-4">Your cart is empty!</h2>;
        }
        return (
            <div className="row">
                <div className="col-lg-9 col-md-9 col-sm-12">
                    {cartDetails}
                </div>
                <div className="col-lg-3 col-md-3 col-sm-12">
                    {orderSummary}
                </div>
            </div>
        );
    }
}

export default withRouter(Cart);
//render the card with horizontal layout and give the total at the right column